// Media
@media screen and(max-width:1480px){
    .header_contaner_main_par{
        margin:0 auto;
    }
}

@media screen and(max-width:829px){
    #arkvarden{
        display: none;
    }
    #timemarkket{
        display: none;
    }
    .ortographick{
        height:auto;
        padding-bottom:10px;
        width:50px;
    }
    .header_contaner_main__navbar__list li{
        width:30px;
        margin:20px auto 0px auto;
    }
    .header_contaner_main__navbar__list li a svg{
        width:30px
    }
    .header_contaner_main__navbar{
        margin-top:50px;
    }
}
@media screen and(max-width:450px){
