.helperdagerinser{
    width:700px;
    max-width: 100%;
    margin:50px auto 0px auto;
    
}
.helperdagerinser__senedr{
    text-align:center;
    font-size:30px;
    text-transform: uppercase;
    padding-top:30px;
    font-family: "Montserrat-Regular";
    color:#333;
}
.helperdagerinser__senedr span{
    color:#826796
}
.helperdagerinser__report{
    text-align: center;
    font-size:17px;
    font-family: "Montserrat-Regular";
    padding-top:12px;
}
.input_label_emailer_custom{
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: bold;
    font-size: 17px;
    line-height: 18px;
    color: #826796;
    
}
.input_label_emailer_div{
    margin-top:70px;
}
.input_label_emailer_div input{
    display: block;
    margin-top:10px;
    height: 50px;
    width:500px;
    max-width: 100%;
    background: #f0eaed;
    border: 0.5px solid #826796;
    box-sizing: border-box;
    border-radius: 6px;
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: rgba(0, 0, 0, 0.65);
    padding-left: 17px;
    padding-right: 17px;
}
.input_label_emailer_div_message{
    margin-top:40px;
}
.input_label_emailer_div_message textarea{
    display: block;
    width:500px;
    max-width: 100%;
    height: 250px;
    margin-top:10px;
    background: #f0eaed;
    border: 0.5px solid #826796;
    box-sizing: border-box;
    border-radius: 6px;
    font-family: "Montserrat-Regular";
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: rgba(0, 0, 0, 0.65);
    padding-left: 17px;
    padding-top: 17px;
}



@media screen and (max-width:768px){
    .helperdagerinser{
        width:90%;
    }
}