.if_user_role_isowner {
  background: none;
  width: 96px;
  height: 31px;
  max-width: 100%;
  text-transform: uppercase;
  font-family: "Montserrat-Regular";
  border: 1px solid #963636;
  box-sizing: border-box;
  border-radius: 16px;
  color: #963636;
  transition: all 0.5s ease;
  cursor: default;
}
.if_user_role_isuser {
  background: none;
  width: 96px;
  height: 31px;
  max-width: 100%;
  text-transform: uppercase;
  font-family: "Montserrat-Regular";
  border: 1px solid #3b6392;
