  border-radius: 16px;
  color: #3b6392;
  transition: all 0.5s ease;
  cursor: default;
}
.if_user_role_isadmin {
  background: none;
  width: 96px;
  height: 31px;
  max-width: 100%;
  text-transform: uppercase;
  font-family: "Montserrat-Regular";
  border: 1px solid #e8ac71;
  box-sizing: border-box;
  border-radius: 16px;
  color: #e8ac71;
  transition: all 0.5s ease;
  cursor: default;
}
.if_user_role_isadmin:hover {
  background: #e8ac71;
  color: #fff;
  transition: all 0.5s ease;
}
.if_user_role_isowner:hover {
  background: #963636;
  color: #fff;
  transition: all 0.5s ease;
}
.if_user_role_isuser:hover {
