.switch {
  position: absolute;
  display: inline-block;
  width: 140px;
  height: 34px;
  left: 20px;
  top: 30px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.perYear {
  position: absolute;
  z-index: 9999;
  top: 8px;
  left: 20px;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  color: white;
}
.perYear1 {
  position: absolute;
  z-index: 9999;
  top: 7px;
  right: 7px;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  color: rgba(0, 0, 0, 0.81);
}
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid #826796;
  background-color: #f9f5f5;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 33px;
  width: 80px;
  bottom: 0px;
  background: #826796;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #f9f5f5;
  border: 1px solid #826796;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(60px);
  -ms-transform: translateX(60px);
  transform: translateX(60px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 34px;
}
