@font-face {
  font-family: "TTSupermolotCondensed-Light";
  src: local("TTSupermolotCondensed-Light"),
    url("./fonts/TTSupermolotCondensed-Light.woff2") format("woff2"),
    url("./fonts/TTSupermolotCondensed-Light.woff") format("woff"),
    url("./fonts/TTSupermolotCondensed-Light.ttf") format("truetype"),
    url("./fonts/TTSupermolotCondensed-Light.eot") format("eot");
  font-display: swap;
}
@font-face {
  font-family: "TTSupermolotCondensed-Bold";
  src: local("TTSupermolotCondensed-Bold"),
    url("./fonts/TTSupermolotCondensed-Bold.woff2") format("woff2"),
    url("./fonts/TTSupermolotCondensed-Bold.woff") format("woff"),
    url("./fonts/TTSupermolotCondensed-Bold.ttf") format("truetype"),
    url("./fonts/TTSupermolotCondensed-Bold.eot") format("eot");
  font-display: swap;
}
@font-face {
  font-family: "TTSupermolotCondensed-Regular";
  src: local("TTSupermolotCondensed-Regular"),
    url("./fonts/TTSupermolotCondensed-Regular.woff2") format("woff2"),
    url("./fonts/TTSupermolotCondensed-Regular.woff") format("woff"),
    url("./fonts/TTSupermolotCondensed-Regular.ttf") format("truetype"),
    url("./fonts/TTSupermolotCondensed-Regular.eot") format("eot");
  font-display: swap;
}
@font-face {
  font-family: "Technic_Regular";
  src: local("Technic_Regular"),
    url("./fonts/Technic_Regular.ttf") format("truetype");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat-Regular";
  font-style: normal;
  src: local("Montserrat-Regular"),
    url("./fonts/Montserrat-Regular.woff2") format("woff2"),
    url("./fonts/Montserrat-Regular.woff") format("woff"),
    url("./fonts/Montserrat-Regular.ttf") format("truetype"),
    url("./fonts/Montserrat-Regular.eot") format("eot");
  font-display: swap;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
select,
select:active,
select:focus {
  outline: none;
}
button,
button:active,
button:focus {
  outline: none;
}
input,
input:active,
input:focus {
  outline: none;
}
textarea,
textarea:active,
textarea:focus {
  outline: none;
}
button {
  cursor: pointer;
}
