.checkbox input[type="radio"] {
  display: none; }

.checkbox {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap; }

.checkbox label::before {
  content: "";
  background: url("./img/ellipsing.png");
  display: inline-block;
  height: 15px;
  width: 15px;
  cursor: pointer; }

.checkbox label::after {
  content: "";
  margin-top: -15px;
  margin-left: -1px;
  cursor: pointer; }

.checkbox label {
  position: relative; }

.checkbox label::before,
.checkbox label::after {
  position: absolute; }

/* */
.checkbox label::before {
  left: 1px; }

/**/
.checkbox label::after {
  left: 5px;
  top: 17px; }

/*   */
.checkbox input[type="radio"] + label::after {
  content: none; }

/*    checked*/
.checkbox input[type="radio"]:checked + label::after {
  content: url("./img/circle.png"); }
