.middlerconsumer{
    width:250px;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
    margin:30px auto 0px auto;
}
.middlerconsumer button:nth-child(1){
    width:130px;
    max-width:100%;
    height:40px;
    color:#826796;
    background:none;
    font-family: 'Montserrat-Regular';
    text-transform: uppercase;
    font-size: 14px;
    transition:all .5s ease;
    border: 1px solid #826796;
    border-radius: 20px;
}
.formerrefistr__serire_seconds__kordo img{
    display: block;
    margin:0px auto 0px auto;
}
.middlerconsumer button:nth-child(2){
    width:100px;
    max-width:100%;
    text-transform: uppercase;
    height:40px;
    font-family: 'Montserrat-Regular';
    background:none;
    font-size: 14px;
    transition:all .5s ease;
    border: 1px solid #826796;
    border-radius: 20px;
}
.formerrefistr{
    display: flex;    
    width:500px;
    max-width: 100%;
    margin:70px auto 0px auto;
    flex-wrap: wrap;
}
.formerrefistr__serire{
    width:500px;
    max-width: 100%;
    height:auto;
    padding-bottom:25px;
    background:rgba(255, 250, 210, 0.4);
 
}
.formerrefistr__serire_seconds{
    width:500px;
    max-width: 100%;
    height:auto;
    padding-bottom:30px;
    background:linear-gradient(180deg, #826796 35.42%, #CD8946 100%);
    border-radius: 6.5px 6.5px 6.5px 6.5px;
    margin:0 auto;
}
.formerrefistr__serire_seconds1{
    width:500px;
    max-width: 100%;
    height:auto;
    padding-bottom:25px;
    background:#F9F5F5;
    margin:0 auto;
    border-radius: 6px 6px 6px 6px
}
.formerrefistr__serire_seconds__kordo{
    width: 100%;
    height:35px;
    background: #F9F5F5;
    border-radius: 6px 6px 0px 0px;
}
.formerrefistr__serire_petro21{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.1em;
    color: #F9F5F5;
    text-align: center;
    margin-top:40px;
}
.formerrefistr__serire_petro12{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 300;
    font-size: 12px;
    line-height: 15px;
    color: #F9F5F5;
    text-align: center;
}
.formerrefistr__serire_seconds form {
    width:400px;
    max-width: 100%;
    margin:0 auto;
}
.formerrefistr__serire_seconds1 form {
    width:400px;
    max-width: 100%;
    margin:0 auto;
}
.formerrefistr__serire_petro{
    font-size:34px;
    font-family: 'TTSupermolotCondensed-Regular';
    text-align: center;
    padding-top:50px;
    text-transform: uppercase;
}
.formerrefistr__serire_petro_registr{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.1em;
    color: #826796;
    text-align: center;
    text-transform: uppercase;
    margin-top:21px;
}
.formerrefistr__serire span{
    color:tomato
}
.formerrefistr__serire ul{
    list-style: upper-alpha;
    width:400px;
    max-width: 100%;
    margin:40px auto 0px auto;

}
.formerrefistr__serire ul li{
    margin-top:30px;
    font-family: 'TTSupermolotCondensed-Regular';
    font-size:20px;
}
.formerrefistr__serire_petrolin{
    font-size:20px;
    font-family: 'TTSupermolotCondensed-Regular';
    text-align: center;
    padding-top:10px;
    text-transform: uppercase;
}


.formerrefistr__serire_petro1{
    font-size:12px;
    margin-top:40px;
    line-height: 23px;
    color:white;
    font-weight:300;
    font-family: 'Montserrat-Regular';
}
.formerrefistr__serire_petro1_reg{
    font-size:12px;
    margin-top:40px;
    line-height: 23px;
    color: rgba(0, 0, 0, 0.65);
    font-weight:300;
    font-family: 'Montserrat-Regular';
}
.salgertodocustom span{
    color:#FABE46
}
.readmoreabout{
    width:250px;
    height:40px;
    background:tomato;
    border:none;
    font-family: 'TTSupermolotCondensed-Regular';
    color:white;
    font-size:19px;
    margin-top:30px;
    margin-left:30px;
}
.heading_author{
    text-align: center;
    font-size:28px;
    text-transform: uppercase;
    padding-top:50px;
    font-family: 'Montserrat-Regular';
    word-wrap: break-word;
}
.toggler_classfor_valid{
    font-size:15px;
    text-align: center;
    color:tomato;
    font-family: 'TTSupermolotCondensed-Regular';
    margin-top:7px;
}
.signinformcustomer__input_label{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #F9F5F5;
    text-transform: capitalize;
}
.signinformcustomer__input_label_reg{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color:  rgba(0, 0, 0, 0.72);
  
}
.firstoinput_signin1{
    margin-top:50px;
}
.signinformcustomer__input{
    width:400px;
    height:40px;
    max-width: 100%;
    padding-left:5px;
    margin-top:8px;
    font-size:16px;
    background: rgba(249, 245, 245, 0.84);
    border: 0.5px solid #826796;
    box-sizing: border-box;
    border-radius: 6px;
    font-family: 'Montserrat-Regular';
  
}
.firstoinput_signin2{
    margin-top:35px;
}
.firstoinput_signin212{
    margin-top:25px;
}
.linkin_forgotpassword{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #F9F5F5;

}
.linkin_forgotpassword{
    margin-top:8px;
}
.linkin_forgotpassword a{
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #F9F5F5;
    text-decoration: none;
}
.signinformcustomer__button{
    width:110px;
    height:37px;
    color: #F9F5F5;
    border:none;
    font-family: 'Montserrat-Regular';
    background: #826796;
    border-radius: 16px;
    font-size:14px;
    display: block;
    margin:30px auto 0px auto;
    transition: all .5s ease;
}
.signinformcustomer__button:hover{
    transition: all .5s ease;
    color:#826796;
    background: #F9F5F5;
    border:1px solid #826796;
}
.signinformcustomer__button_regd{
    width:110px;
    height:37px;
    color: #826796;
    border:none;
    font-family: 'Montserrat-Regular';
    background: #F9F5F5;
    border-radius: 16px;
    font-size:14px;
    display: block;
    margin:30px auto 0px auto;
    transition: all .5s ease;
    border:1px solid #826796;
}
.signinformcustomer__button_regd:hover{
    transition: all .5s ease;
    color: #F9F5F5;
    background: #826796;
   
}

@media screen and (max-width:510px){
    .middlerconsumer button:nth-child(1){
        font-size:11px;
        width:110px;
        height:35px
    }
    .middlerconsumer button:nth-child(2){
        font-size:11px;
        width:110px;
        height:35px
    }
    .heading_author{
        font-size:20px;
    }
    .formerrefistr{
        width:320px;
    }
    .formerrefistr__serire_petro1{
        text-align: center;
    }
    .formerrefistr__serire_petro1_reg{
        text-align:center;
    }
    .linkin_forgotpassword{
        text-align: center;
    }
  .formerrefistr__serire_seconds form{
    width:300px;
  }
  .formerrefistr__serire_seconds1 form{
    width:300px;   
  }
 
}