.containerbootstrap{
    width:1540px;
    max-width: 100%;
    margin:80px auto 0px auto;
    height:auto;
}
.article_mytodo{
   width:99%;
   max-width: 100%;
   background:#F9F5F5;
   height:50px;
   margin-top:15px;
   border: 0.5px solid #826796;
   box-sizing: border-box;
   border-radius: 6px;
   display: flex;
   justify-content: space-between;
}
.article_mytodo div{
   position: relative;
}

.article_mytodo div:nth-child(1){
    width:96px;
    max-width: 100%;
    height:50px;
    
    box-sizing: border-box;
}
.article_mytodo div:nth-child(2){
    width:180px;
    max-width: 100%;
    height:50px;
    
    box-sizing: border-box;
}
.article_mytodo div:nth-child(3){
    width:347px;
    max-width: 100%;
    height:50px;
  
    box-sizing: border-box;
}
.article_mytodo div:nth-child(4){
    width:236px;
    max-width: 100%;
    height:50px;
  
    box-sizing: border-box;
}
.article_mytodo div:nth-child(5){
    width:206px;
    max-width: 100%;
    height:50px;
    
    box-sizing: border-box;
}
.article_mytodo div:nth-child(6){
    width:193px;
    max-width: 100%;
    height:50px;
 
    box-sizing: border-box;
}
.article_mytodo div:nth-child(7){
    width:156px;
    max-width: 100%;
    height:50px;

    box-sizing: border-box;
}
.article_mytodo div:nth-child(8){
    width:106px;
    max-width: 100%;
    height:50px;
    border-right:none;
    box-sizing: border-box;
   
}

.article_mytodo div p{
    position: absolute;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 19px;
    color: rgba(0, 0, 0, 0.67);
    text-align: center;
    overflow: hidden;
    max-height: 60px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    white-space: normal;
    text-overflow: ellipsis;
}
.article_mytodo div:nth-child(1) p{
    color: #826796;
}
.article_mytodo div:nth-child(4) p{
    color: #826796;
}
.article_mytodo button{
    width:96px;
    position: absolute;
    height:31px;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    cursor:default
}
.waiting_button{
    font-family: 'Montserrat-Regular';
    border: 1px solid #F3C523;
    box-sizing: border-box;
    border-radius: 16px;
    background:none;
    font-weight: 600;
    font-size: 11px;
    line-height: 13px;
    text-align: center;
    color: #F3C523;
    text-transform: uppercase;
    transition: all .5s ease;
}
.waiting_button:hover{
    color:white;
    background:#F3C523;
    transition: all .5s ease;
}
.success_button{
    font-family: 'Montserrat-Regular';
    border: 1px solid #93C192;
    box-sizing: border-box;
    border-radius: 16px;
    background:none;
    font-weight: 600;
    font-size: 11px;
    line-height: 13px;
    text-align: center;
    color: #93C192;
    text-transform: uppercase;
    transition: all .5s ease;
}
.success_button:hover{
    transition: all .5s ease;
    color:white;
    background:#93C192;
}











.my_todolist_all_item{
        width:100%;
        max-width: 100%;
        height:660px;
        margin:0px auto 0px auto; 
        overflow-y: scroll;
}
.my_todolist_all_item::-webkit-scrollbar-thumb {
    background-color: #826796; 
}
.my_todolist_all_item::-webkit-scrollbar-track {
    background: #EBDCCE; }
.my_todolist_all_item::-webkit-scrollbar {
    width: 4px;
} 
.my_todolist_all_item div:nth-child(1){
    margin-top:0px;
}
.my_todolist_all_item_div{
    width:700px;
    height:auto;
    padding-bottom:13px;
    max-width:100%;
    margin:40px auto 0px auto;
    position: relative;  
     border-radius: 5px;
}
.my_todolist_all_item_div_p{
    text-align: center;
    font-size:20px;
    padding-top:10px;
}
.my_todolist_all_item_div_img{
    position: absolute;
    top:8px;
    width:21px;
    right:5px;
    cursor: pointer;
}
.my_todolist_all_item_div_img_panel{
    position: absolute;
    top:9px;
    width:21px;
    right:30px;
    cursor: pointer;
}
.cursor_changer{
 
    cursor: url('./img/pencil.png'), auto;	
}
.togglerdisplay{
    display:none;
}
.my_todolist_all_item_div_p_text{
    font-size:18px;
    padding-left:10px;
    padding-top:7px;    
    font-family: 'TTSupermolotCondensed-Light';
    
}
.my_todolist_all_item_div_p_text_timer{
    font-size:18px;
    text-align: right;
    padding-right:5px;
    font-family: 'TTSupermolotCondensed-Light';
}
.my_todolist_all_item_div_p_time{
    
    font-family: 'TTSupermolotCondensed-Light';
    text-align: right;
    padding-right:7px;
    font-size:20px;
}
.headhingformetodo{
    text-align: center;
    font-size: 40px;
    font-family: 'TTSupermolotCondensed-Regular';
    margin-top:40px;
}
.clakson{
    font-size:25px;
    color:red;
    font-family: 'TTSupermolotCondensed-Regular';
}
.claksonmain{
    font-size:25px;
    text-align: center;
    margin-top:15px;
    font-family: 'TTSupermolotCondensed-Regular';
}
.search__input{
    width:300px;
    height:40px;
    font-size:18px;
    max-width: 100%;
    font-family: 'TTSupermolotCondensed-Light';
    box-sizing: border-box;
    padding-left:10px;
    border:1px solid lightgrey;
    border-right:none;
}
.searchpanel{
    display:flex;
    width:360px;
    max-width: 100%;
    margin:20px auto 0px auto;
}
.toolbarion{
    font-size:25px;
    font-family: 'TTSupermolotCondensed-Regular';
    text-align: center;
}
.searchpanel button{
    background: none;
    border:1px solid lightgrey;
    border-left:none;
}
.searchpanel button svg{
    fill:#263B47;
    transition:all .5s ease;

}
.searchpanel button svg:hover{
    fill:tomato;
    transition: all .5s ease;
}
