#daydorday{
    width:900px;
    max-width: 100%;
    height:auto;
    margin:0 auto;
    padding-bottom:30px;
}
#monthlydayly{
    display: flex;;
}
.moyalobofer{
    font-family: 'Montserrat-Regular';
    font-size:30px;
    margin-left:9px;
}
#monthlydayly p{
  text-align: center;
  font-size:30px;
  font-family: 'Montserrat-Regular';
}
.table_classiksa{
    margin:20px auto 0px auto;
    width:100%;
    border-spacing: 10px 10px;
}
.table_classiksa tr th{
    font-size:30px;
    width:100px;
    padding-bottom:20px;
 
  
    font-family: 'Montserrat-Regular';
}
.table_classiksa tr td{
    text-align: left;
    font-size:20px;
    height:90px;
    padding-left:8px;
    padding-top:8px;
    font-family: 'Montserrat-Regular';
    border:1px solid #e4e4e4;
    transition: all .5s ease;
}
.table_classiksa tr td:hover{
    transition: all .5s ease;
    background:rgba(196, 196, 196, 0.36);
}
.secondSliderGeorge{
    display: flex;
    width:300px;
    margin:0 auto;
    justify-content: space-between;
    height:80px;
    margin-top:70px;
    align-items: center;
}
.secondSliderGeorge button{
    border:none;
    background:none;
    
}
.circlerclassfortask{
    width:10px;
    height:10px;
    border-radius: 50%;
    background:#826796;
    margin-top:25px;
    margin-right:3px;
    float:right;
    border:1px solid #826796
}

.perYear2{
    position:absolute;
    z-index: 9999;
    top:8px;
    left:20px;
    font-family: 'Montserrat-Regular';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    color: rgba(0, 0, 0, 0.81);

}
.perYear3{
  position:absolute;
  z-index: 9999;
  top:8px;
  right:7px;
  font-family: 'Montserrat-Regular';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 15px;
  color:white;
 
}
.div_for_notiv_modal{
    position:absolute;
    top:50%;
    left:50%;
    width:450px;
    height:250px;
    max-width: 100%;
    background:red;
    transform: translate(-50%,-50%);
    display: none;
}




