@media screen and (max-width:650px){
    .circlerclassfortask{
        width:5px;
        height:5px;
        margin-right: 1px;
    }
}
@media screen and (max-width:540px){
    .moyalobofer{
        font-size:22px;
    }
    .table_classiksa tr th{
        font-size:17px;
    }
    #monthlydayly p{
        font-size:22px;
    }
}