
.textupperlowdag{
    font-size:40px;
    text-align: center;
    font-family: 'Montserrat-Regular';
    color:#7D3DAF;
    padding-top:10px;
}

.textupperlowdag3{
    font-size:30px;
    text-align: center;
    font-family: 'Montserrat-Regular';
    color:#7D3DAF;
    padding-top:10px;
}
.heading_viber_notification{
    text-align: center;
    margin-top:70px;
    font-size:35px;
    font-family: 'Montserrat-Regular';
}
.heading_viber_notification span{
    color:#7D3DAF
}
.vibercontentnotic__flexcont{
    width:1000px;
    margin:100px auto 0px auto;
    max-width: 100%;
    height:auto;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}
.vibercontentnotic__flexcont_first{
    width:500px;
    max-width: 100%;
    background:rgba(125, 61, 175, 0.3);
    height:550px;
}
.vibercontentnotic__flexcont_second{
    width:500px;
    max-width: 100%;
    background:white;
    height:550px;
}
.vibercontentnotic__flexcont_first_hex{
    font-size:34px;
    font-family: 'TTSupermolotCondensed-Regular';
    text-align: center;
    padding-top:50px;
    text-transform: uppercase;
   
}
.vibercontentnotic__flexcont_first_he{
    font-size:24px;
    font-family: 'TTSupermolotCondensed-light';
    text-align: center;
    padding-top:10px;
    text-transform: uppercase;
}
.vibercontentnotic__flexcont_first ul{
    list-style: upper-alpha;
    width:400px;
    max-width: 100%;
    margin:50px auto 0px auto;

}
.vibercontentnotic__flexcont_first ul li{
    margin-top:30px;
    font-family: 'TTSupermolotCondensed-Regular';
    font-size:20px;
    text-align: justify;
}
.vibercontentnotic__flexcont_second img{
    display: block;
    width:180px;
    max-width: 100%;
    margin:5px auto 0px auto;
}
.viber_finish_settings{
    font-size:17px;
    width:420px;
    max-width: 100%;
    margin:0 auto;
    font-family: 'TTSupermolotCondensed-Regular';
    text-align: left;
    line-height:23px;
}
.vibercontentnotic__flexcont_second button{
    width:200px;
    max-width: 100%;
    height:50px;
    background:rgba(125, 61, 175, 0.7);
    border:none;
    font-size: 20px;;
    font-family: 'TTSupermolotCondensed-light';
    color:white;
    display: block;
    margin:20px auto 0px auto;
}
#codeclone{
    height:55px;
}
.viber_finish_settings_end{
    width:420px;
    max-width: 100%;
    margin:5px auto 0px auto;
    font-size: 17px;
    font-family: 'TTSupermolotCondensed-Regular';
}
.viber_finish_settings_end a{
    color:#42E860;
}
.viber_finish_settings_end_left{
    width:435px;
    max-width: 100%;
    margin:30px auto 0px auto;
    font-family: 'TTSupermolotCondensed-Regular';
    font-size:17px;
    
}
.heading_after_heading{
    text-align: center;
    font-size:23px;
    font-family: 'Montserrat-Regular';
    padding-top:8px;
}
